<template>
  <div class="page">
    <h1>The m-labs Documentation</h1>
    <p>
      The m-labs is a library of vue components and sass styles built (By:
      <a href="https://moneerAlDakkak.github.io/Home">Moneer Al-Dakkak </a>)
      specially for Vue-CLI.
    </p>
    <h2>Install the library with npm</h2>
    <p>
      <m-code type="block" code="npm install m-labs" />
    </p>
    <article>
      <h2>Registering Vue components</h2>
      <p>
        import any component using object destructuring as shown in the code :
      </p>
      <m-code type="block" code="import { MButton } from 'm-labs'" />
      <p>
        Ypu can also register Vue components globaly in your
        <m-code code="main.js" /> by importing the m object and implementing a
        simple loop as shown in the code below :
      </p>
      <m-code
        type="block"
        language="Js"
        code="
import { createApp } from 'vue';
import App from './App.vue';
import './registerServiceWorker';
import router from './router';
import store from './store';

const app = createApp(App);

import { m } from 'm-labs';

for (let component in m) {
  app.component(component, m[component]);
}

app.use(store).use(router).mount('#app');

"
      />
    </article>
    <article>
      <h2>Using Sass features</h2>
      <p>
        Import the <m-code code="_m.scss" /> file to have all the variables,
        mixins and functions. However, there is no need to import the library's
        sass if you don't want to use any of the features in your project (the
        Vue components will still work).
      </p>
      <m-code
        type="block"
        language="Sass"
        code="
<style lang='scss' scoped>
  @use '@/../node_modules/m-labs/sass/m' as *;
</scss>

"
      />
    </article>
  </div>
</template>

<script>
export default {
  name: "Intro",
};
</script>

<style lang="scss" scoped></style>
